.home-container {
  position: relative;
}

.video-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden; /* Added to prevent video overflow */
}

.video-background video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.content {
  position: relative;
  z-index: 1;
  color: white; /* Added to set the text color */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.heading {
  text-align: right;
  margin-top: 60px;
}


.table-container {
  margin-top: 20px; 
}

.logout-container {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 0px;
  margin-left: 750px;
}
